import { Component, EventEmitter, Output } from '@angular/core';
import { ButtonModule } from 'primeng/button';

@Component({
  selector: 'lib-pla-button-save',
  imports: [ButtonModule],
  templateUrl: './pla-button-save.component.html',
  styleUrl: './pla-button-save.component.css'
})
export class PlaButtonSaveComponent {
  @Output() onSave = new EventEmitter<void>();

  onSaveClick() {
    this.onSave.emit();
  }
}
